<template>
  <section class="notification is-success hero-sticky">
    <div class="columns is-mobile">
      <div class="column is-one-third has-text-centered">
        <p class="is-size-4 mt-2">
          <strong>{{ days }} días necesarios</strong>
        </p>
      </div>
      <div class="column is-one-third has-text-centered">
        <p class="is-size-4 mt-2">
          <strong>Costo: {{ cost }} $us</strong>
        </p>
      </div>
      <div class="column is-one-third has-text-centered">
        <button
          class="button is-success is-medium"
          @click="send()"
          :disabled="quota >= 10"
        >
          <strong>ENVIAR</strong>
        </button>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "Costs",
  data: () => ({
    quota: 0,
  }),
  created() {
    if (localStorage.quota) {
      this.quota = localStorage.quota;
    }
  },
  props: {
    days: Number,
    cost: Number,
  },
  methods: {
    send() {
      this.$emit("send");
    },
  },
};
</script>

<style>
.hero-sticky {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0rem;
  align-self: flex-end;
  width: 100%;
  z-index: 999;
  padding: 1.5rem 2rem;
}
</style>
