<template>
  <div>
    <div class="container is-fluid mx-4 py-3">
      <div v-for="(section, i) in sections" :key="i">
        <!-- control fields -->
        <div class="field">
          <label class="label">{{ section.title }}</label>

          <!-- multiple checkboxes -->
          <div class="control" v-if="section.multiSelect">
            <div v-for="(option, j) in section.options" :key="j">
              <label class="checkbox">
                <input type="checkbox" v-model="option.selected" />
                {{ option.title }}
              </label>
            </div>
          </div>

          <!-- dropdown select -->
          <div class="control" v-else>
            <div class="select is-fullwidth">
              <select v-model="section.option">
                <option disabled :value="{ title: '', days: 0 }">
                  Opciones...
                </option>
                <option
                  v-for="(option, j) in section.options"
                  :key="j"
                  :value="option"
                >
                  {{ option.title }}
                </option>
              </select>
            </div>
          </div>
        </div>

        <hr class="my-1" />
      </div>
    </div>
    <Costs
      v-if="days > 0"
      :days="days"
      :cost="days * developeDayRate"
      @send="send()"
    />
  </div>
</template>

<script>
import Costs from "@/components/Costs.vue";
import { db } from "../db";

export default {
  data: () => ({
    sections: [],
    developeDayRate: 35,
    quota: 0,
  }),
  created() {
    this.loadSections();
    const now = new Date();
    const today = now.getMonth() + "" + now.getDate();
    if (localStorage.today && localStorage.quota) {
      if (today == localStorage.today) {
        this.quota = localStorage.quota;
      } else {
        localStorage.today = today;
        localStorage.quota = 0;
      }
    } else {
      localStorage.today = today;
      localStorage.quota = 0;
    }
  },
  components: {
    Costs,
  },
  methods: {
    loadSections() {
      this.sections = [
        {
          title: "Qué tipo de aplicación es?",
          multiSelect: false,
          option: { title: "", days: 0 },
          options: [
            { title: "Web estática", days: 1 },
            { title: "Web app", days: 5 },
            { title: "Móvil (Android)", days: 5 },
            { title: "Móvil (iOS)", days: 5 },
            { title: "Móvil (Multiplataforma)", days: 7 },
          ],
        },
        {
          title: "Qué tan grande es tu aplicación?",
          multiSelect: false,
          option: { title: "", days: 0 },
          options: [
            { title: "Pequeña", days: 15 },
            { title: "Mediana", days: 30 },
            { title: "Grande", days: 60 },
          ],
        },
        {
          title: "Qué tan sofisticada necesita el diseño de la App?",
          multiSelect: false,
          option: { title: "", days: 0 },
          options: [
            { title: "Minimo", days: 5 },
            { title: "Normal", days: 10 },
            { title: "Sofisticada", days: 20 },
          ],
        },
        {
          title: "Usuarios y cuentas",
          multiSelect: true,
          options: [
            {
              title: "Inicio de sesión Email/Contraseña",
              days: 1,
              selected: false,
            },
            {
              title: "Inicio de sesión Facebook",
              days: 0.5,
              selected: false,
            },
            {
              title: "Inicio de sesión Twitter",
              days: 0.5,
              selected: false,
            },
            {
              title: "Inicio de sesión Google",
              days: 0.5,
              selected: false,
            },
            {
              title: "Inicio de sesión Linked In",
              days: 0.5,

              selected: false,
            },
            {
              title: "Inicio de sesión TikTok",
              days: 0.5,
              selected: false,
            },
            {
              title: "Inicio de sesión Github",
              days: 0.5,
              selected: false,
            },
            {
              title: "Invitaciones por email",
              days: 3,
              selected: false,
            },
            {
              title: "Cuentas con perfiles (Tipo Netflix)",
              days: 6,
              selected: false,
            },
          ],
        },
        {
          title: "Contenido generado por usuarios",
          multiSelect: true,
          options: [
            { title: "Dashboard", days: 6, selected: false },
            {
              title: "Feed de la actividad",
              days: 5,
              selected: false,
            },
            {
              title: "Almacenamiento de archivos *",
              days: 1,
              selected: false,
            },
            {
              title: "Perfiles",
              days: 5,
              selected: false,
            },
            {
              title: "Emails desde la app",
              days: 1,
              selected: false,
            },
            {
              title: "Formularios",
              days: 1,
              selected: false,
            },
            {
              title: "Etiquetas y categorías",
              days: 1,
              selected: false,
            },
            {
              title: "Reviews y ratings",
              days: 6,
              selected: false,
            },
            {
              title: "Reproducción de Audio/Video",
              days: 1,
              selected: false,
            },
            {
              title: "Streaming de Audio/Video",
              days: 9,
              selected: false,
            },
            {
              title: "Manipulación de multimedia",
              days: 3,
              selected: false,
            },
            {
              title: "Busqueda por texto",
              days: 1,
              selected: false,
            },
          ],
        },
        {
          title: "Funciones específicas para móviles",
          multiSelect: true,
          options: [
            {
              title: "Diseño de icono/splash-screen de aplicación",
              days: 2,
              selected: false,
            },
            {
              title: "Sincronización en la nube (Tiempo real)",
              days: 12,
              selected: false,
            },
            {
              title: "Datos del sensor del dispositivo",
              days: 6,
