            },
            {
              title: "Códigos de barras o códigos QR",
              days: 1,
              selected: false,
            },
            {
              title: "Datos de salud",
              days: 5,
              selected: false,
            },
          ],
        },
        {
          title: "Fechas y/o servicios con GPS",
          multiSelect: true,
          options: [
            {
              title: "Calendario",
              days: 1,
              selected: false,
            },
            {
              title: "Mostrar mapa / Geolocación",
              days: 2,
              selected: false,
            },
            {
              title: "Mostrar marcados y regiones personalizadas",
              days: 8,
