            },
            {
              title: "Marcadores en el calendario",
              days: 2,
              selected: false,
            },
          ],
        },
        {
          title: "Social & interaciones",
          multiSelect: true,
          options: [
            {
              title: "Mensageria (Chat)",
              days: 7,
              selected: false,
            },
            {
              title: "Foros o comentarios",
              days: 6,
              selected: false,
            },
            {
              title: "Compartir en redes sociales",
              days: 0.5,
              selected: false,
            },
            {
              title: "Conectar con Facebook",
              days: 1,
              selected: false,
            },
            {
              title: "Conectar con Twitter",
              days: 1,
              selected: false,
            },
            {
              title: "Compartir con vista previa (Open Graph)",
              days: 7,
              selected: false,
            },
            {
              title: "Notificaciones push",
              days: 12,
              selected: false,
            },
          ],
        },
        {
          title: "Cobros & eCommerce",
          multiSelect: true,
          options: [
            {
              title: "Planes de subscripción *",
              days: 15,
              selected: false,
            },
            {
              title: "Procesamientos de pagos *",
              days: 6,
              selected: false,
            },
            {
              title: "Carrito de compras",
              days: 2,
