            },
          ],
        },
        {
          title: "Administradores, feedback & estadísticas",
          multiSelect: true,
          options: [
            {
              title: "Integracion de CMS",
              days: 12,
              selected: false,
            },
            {
              title: "Integracion de LMS",
              days: 21,
              selected: false,
            },
            {
              title: "Filtros de spam *",
              days: 3,
              selected: false,
            },
            {
              title: "Paginas administradas",
              days: 4,
              selected: false,
            },
            {
              title: "Moderación/Control de contenido *",
              days: 5,
              selected: false,
            },
            {
              title: "Chat interno",
              days: 4,
              selected: false,
            },
            {
              title: "Estadísticas de uso",
              days: 2,
              selected: false,
            },
            {
              title: "Reportes de crashes",
              days: 0,
              selected: true,
            },
            {
              title: "Soporte multilenguage (Por idioma)",
              days: 7,
              selected: false,
            },
          ],
        },

        {
          title: "APIs externas e integraciones",
          multiSelect: true,
          options: [
            {
              title: "Conéctese a uno o más servicios de terceros",
              days: 6,
              selected: false,
            },
            {
              title: "Una API para que otros la integren con su aplicación",
              days: 30,
              selected: false,
            },
            {
              title: "Mensajería SMS *",
              days: 5,
              selected: false,
            },
            {
              title: "Mensajería WhatsApp *",
              days: 5,
              selected: false,
            },
          ],
        },
        {
          title: "Seguridad",
          multiSelect: true,
          options: [
            {
              title: "Seguridad basada en certificados SSL *",
              days: 1,
              selected: false,
            },
            {
              title: "Protección DDoS",
              days: 6,
              selected: false,
            },
            {
              title: "Autenticación de dos factores *",
              days: 6,
              selected: false,
            },
            {
              title: "Dominio personalizado *",
              days: 5,
              selected: false,
            },
          ],
        },
      ];
    },
    send() {
      let characteristics = [];
      let days = 0.0;

      for (const section of this.sections) {
        let selectedSection = { title: section.title, options: [] };
        if (section.multiSelect) {
          for (const option of section.options) {
            if (option.selected) {
              selectedSection.options.push({
                title: option.title,
                days: option.days,
              });
              days += option.days;
            }
          }
        } else {
          selectedSection.options.push({
            title: section.option.title,
            days: section.option.days,
          });
          days += section.option.days;
        }

        if (selectedSection.options.length > 0) {
          characteristics.push(selectedSection);
        }
      }

      const data = {
        characteristics,
        days,
        cost: days * this.developeDayRate,
      };
      if (this.quota < 10) {
        db.collection("costs").add(data);
      }
      this.loadSections();
      this.incrementQuota();
    },
    incrementQuota() {
      this.quota++;
      localStorage.quota = this.quota;
    },
  },
  computed: {
    days() {
      let days = 0.0;
      for (const section of this.sections) {
        if (section.multiSelect) {
          for (const option of section.options) {
            if (option.selected) {
              days += option.days;
            }
          }
        } else {
          days += section.option.days;
        }
      }
      return days;
    },
  },
};
</script>
